#panel-header-settings > *, #header-bg-settings > *, #header-right-settings > *, #header-left-settings > *, #header-title-settings > *{
    background-color: #5690ff !important;
    z-index: 5 !important;
}
.settingsTitleContainer{
    padding: 0 0 16px 0;
}

.settingsTitle{
    font-size: 14px;
    font-weight: bold;
    color: #5690ff;
}

.groupSettingsContainer{
    background-color: #fff;
    box-shadow: 0 4px 4px 0 #d9d9d9;
    padding: 15px 0 12px 16px;
    margin-bottom: 12px;
}

.groupSettingsContainer > button{
    margin-top: 0 !important;
    padding: 0;
}

.settingsSettingContainer{
    width: 100%;
    min-height: 30px;
    /*padding: 0 0 0 16px;*/
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    border-radius: 0 !important;
}

.settingsSettingContainer > div{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.settingsSettingContainer > div > .Button__content{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
}

.settingsSettingContainer > div > div{
    color: black;
    font-size: 17px !important;
}

.settingsSettingInfo{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 30px;
    height: 100%;
    width: 100%;
    padding: 3px 0 3px 0;
    margin-left: 15px;
}

.settingsSettingTitle{
    display: flex;
    flex-direction: row;
    font-size: 16px !important;
    color: black;
}

.settingsSettingSwitch{
    margin-right: 16px;
}

.settingsSettingSwitcher{
    margin-right: 16px;
}