.accountUserContainer{
    width: 100%;
    height: 48px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 14px;
    box-shadow: 0 5px 15px 0 rgba(27, 27, 78, 0.1);
    background-color: #fff;
    /*margin: 0 10px 10px 10px;*/
}

.accountUserNumeration{
    width: 26px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    color: rgb(60 60 60);
    background-color: rgb(246 246 246);
    border-radius: 14px 0 0 14px;
}

.accountUserInfo{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.accountUserName{
    color: rgb(60 60 60);
    font-size: 15px;
}

#overPlace{
    color: rgb(174 179 192);
    font-size: 15px;
}